@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;600&family=Oswald:wght@500&family=Quicksand:wght@300&display=swap');


.hero{
    width: 100%;
    height: 100vh;
    position: fixed;
}

.hero img{
    position: absolute;
    z-index: -2;
    top: 0;
    left: 0;
    width: 100%;
    object-fit: cover;
    height: 100vh;
    opacity: .8;
}

.overlay{
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0,.7);
}

.text{
    position: absolute;
    top: 20rem;
    left: 5rem;
    z-index: 5;
    display: flex;
    align-items: center;
    gap: 1rem;
}

.mainButton{
    color: white;
    background-color: orangered;
    padding: 1.5rem 2rem;
    border-radius: 100px;
    z-index: 5;
    position: absolute;
    top: 20rem;
    right: 10rem;
    text-decoration: none;
    font-family: 'Oswald',sans-serif;
    font-size: 1.2rem;
    border: none;
    cursor: pointer;
}

@media only screen and (max-width: 850px) {
    .hero{
        margin-top: -2rem;
        overflow: hidden;
    }
    .text{
        top: 10rem;
        left: 0;
        right: 0;
        margin: auto;
        gap: 1.5rem;
        display: flex;
        flex-direction: column;
    }
    .mainButton{
       top: 25rem;
       left: 0;
       right: 0;
       margin: auto;
       width: 11rem;
    }
}